/* src/App.css */
:root{--bg:#f4f6f8;--card:#ffffff;--muted:#6b7280;--primary:#0f172a;--accent:#1f6feb}
*{box-sizing:border-box}
body{margin:0;font-family:Inter, system-ui, -apple-system, 'Segoe UI', Roboto, 'Helvetica Neue', Arial; background:var(--bg);color:#0f172a}
.pf-header{background:white;border-bottom:1px solid #eef2f7}
.pf-container{max-width:1100px;margin:0 auto;padding:0 18px}
.pf-header-inner{display:flex;align-items:center;justify-content:space-between;padding:14px 0}
.pf-brand{display:flex;align-items:center;gap:12px}
.pf-logo{height:44px;width:44px;object-fit:contain}
.pf-title{font-weight:600}
.pf-sub{font-size:12px;color:var(--muted)}
.pf-controls{display:flex;align-items:center;gap:10px}
.pf-search{padding:8px 12px;border-radius:8px;border:1px solid #e6eef8;width:320px}
.pf-ghost{background:transparent;border:1px solid #e6eef8;padding:8px 10px;border-radius:8px;cursor:pointer;text-decoration:none;color:inherit}
.pf-primary{background:var(--accent);color:white;padding:8px 12px;border-radius:8px;border:none;cursor:pointer;text-decoration:none}
.pf-body{display:flex;min-height:calc(100vh - 130px)}
.pf-sidebar{width:220px;background:#fff;border-right:1px solid #eef2f7;padding:18px}
.pf-sidebar nav ul{list-style:none;padding:0;margin:0}
.pf-sidebar nav a{display:block;padding:10px 8px;color:#0f172a;text-decoration:none;border-radius:6px}
.pf-sidebar nav a:hover{background:#f1f5f9}
.pf-sep{color:var(--muted);margin-top:10px;font-size:12px}
.pf-compact-cta{margin-top:22px;padding:12px;border-radius:8px;background:linear-gradient(180deg,#f8fafc,#ffffff);box-shadow:0 6px 18px rgba(2,6,23,0.03)}
.pf-cta-title{font-weight:600}
.pf-cta-text{font-size:13px;color:var(--muted);margin-top:6px}
.pf-block{display:block;width:100%}
.pf-main{flex:1;padding:28px}
.pf-hero{display:flex;gap:24px;align-items:flex-start}
.pf-hero-left{flex:1}
.pf-hero-right{width:300px}
.pf-h1{font-size:32px}
.pf-lead{color:var(--muted);max-width:640px}
.pf-hero-actions{margin-top:18px;display:flex;gap:12px;align-items:center}
.pf-select{padding:10px;border-radius:8px;border:1px solid #e6eef8}
.pf-outlined{background:transparent;border:1px solid #e6eef8;padding:10px;border-radius:8px;cursor:pointer}
.pf-card{background:var(--card);padding:16px;border-radius:12px;box-shadow:0 8px 20px rgba(2,6,23,0.03)}
.pf-stats{display:flex;flex-direction:column;gap:10px}
.pf-stat{display:flex;justify-content:space-between;align-items:center}
.pf-features{display:grid;grid-template-columns:1fr;gap:12px;margin-top:22px}
@media(min-width:900px){.pf-features{grid-template-columns:repeat(3,1fr)}}
.pf-feature-card{padding:20px;border-radius:10px}
.pf-grid{display:grid;grid-template-columns:1fr;gap:18px}
@media(min-width:1000px){.pf-grid{grid-template-columns:1fr 320px}}
.pf-col-main{min-width:0}
.pf-col-side{min-width:0}
.pf-upload{display:flex;gap:12px;align-items:center;margin-top:12px}
.pf-muted{color:var(--muted)}
.pf-tags{display:flex;gap:8px;flex-wrap:wrap;margin-top:8px}
.pf-tag{background:#f1f5f9;padding:6px 10px;border-radius:999px;font-size:13px}
.pf-results{margin-top:12px}
.pf-placeholder{height:240px;border:1px dashed #e6eef8;border-radius:8px;display:flex;align-items:center;justify-content:center;color:var(--muted)}
.pf-footer{text-align:center;padding:18px;color:var(--muted);border-top:1px solid #eef2f7}
.if-label{display:block;margin-bottom:6px;font-weight:600}
.pf-input{width:100%;padding:10px;border-radius:8px;border:1px solid #e6eef8}
/* Report blur wrapper + overlay */
.report-blur-wrapper {
  position: relative;
}

/* ---- Report blur + overlay styling ---- */

/* main wrapper */
.report-wrapper { position: relative; border-radius: 8px; overflow: visible; }

/* content that will be blurred when user is not authenticated */
.report-content {
  border-radius: 8px;
  overflow: hidden;
  transition: filter 220ms ease, transform 220ms ease;
}

/* when overlay present, blur the content — we rely on overlay element presence for visual effect */
/* but ensure blur only applies visually; aria-hidden is set for accessibility in component */
.report-overlay + .report-content,
.report-content[aria-hidden="true"] {
  filter: blur(6px) saturate(0.92);
  transform: scale(0.997);
}

/* overlay — sits in front of blurred content */
.report-overlay {
  position: absolute;
  inset: 0; /* covers the full area */
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 18px;
  z-index: 60; /* high so it always is in front */
  background: linear-gradient(180deg, rgba(255,255,255,0.65), rgba(255,255,255,0.6));
  cursor: pointer;
  transition: opacity 200ms ease;
}

/* small elevated card inside overlay */
.report-overlay .overlay-inner {
  max-width: 640px;
  width: calc(100% - 48px);
  background: white;
  border-radius: 12px;
  padding: 20px;
  text-align: center;
  box-shadow: 0 12px 36px rgba(2,6,23,0.12);
  transform: translateY(-8px);
  animation: overlay-entrance 240ms ease both;
}

/* entrance animation */
@keyframes overlay-entrance {
  from { opacity: 0; transform: translateY(-12px) scale(0.995); }
  to { opacity: 1; transform: translateY(0) scale(1); }
}

/* card elevated helper (optional) */
.card-elevated { border-radius: 8px; }

/* ensure buttons inside overlay are accessible and visible */
.report-overlay .pf-primary,
.report-overlay .pf-outlined {
  min-width: 140px;
}

/* small responsive tweak */
@media (max-width: 640px) {
  .report-overlay .overlay-inner { padding: 16px; width: calc(100% - 32px); }
}
